.search {
  position: relative;
  padding: 24px;
}

@media (min-width: 992px) {
  .search {
    padding: 40px;
  }
}

.title {
  margin-bottom: 24px;
  font-size: 24px;
  text-transform: uppercase;
}

.stage1 {
  display: flex;
  flex-direction: column-reverse;
}

@media (min-width: 1200px) {
  .stage1 {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
}

.form {
  flex: 1;
  max-width: 984px;
  display: flex;
  flex-direction: column;
}

.inputGroups {
  display: flex;
  flex-direction: column;
}

@media (min-width: 1200px) {
  .inputGroups {
    flex-direction: row;
    align-items: flex-start;
  }
}

.inputGroup {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  &:not(:first-child) {
    margin-top: 32px;
  }
}

@media (min-width: 1200px) {
  .inputGroup {
    width: calc((100% - 48px) / 3);
    max-width: 312px;
    &:not(:first-child) {
      margin-top: 0;
      margin-left: 24px;
    }
  }
}

@media (min-width: 1200px) {
  .inputGroup {
    &:not(:last-child) {
      .indicator {
        margin-right: -16px;
        width: calc(100% + 16px);
      }
    }
  }
}

.inputWrapper {
  margin-top: 16px;
  width: 100%;
}

@media (min-width: 768px) {
  .inputWrapper {
    width: calc((100% - 40px) / 2);
  }
}

@media (min-width: 1200px) {
  .inputWrapper {
    width: 100%;
    &:first-child {
      margin-top: 0;
    }
  }
}

.list {
  margin-bottom: 32px;
}

@media (min-width: 1200px) {
  .list {
    margin-left: 40px;
    margin-bottom: 0;
    width: 400px;
  }
}

.listItems {
  margin-top: 48px;
}

.listItem {
  display: grid;
  grid-template: "text data edit delete" 32px / minmax(0, auto) min-content min-content min-content;
  padding: 8px 0;
  font-size: 13px;
  border-bottom: 1px solid rgba($color: #707070, $alpha: 0.5);
  &:first-child {
    border-top: 1px solid #ef336a;
  }
}

.listItemText {
  grid-area: text;
  display: flex;
  align-items: center;
  & > div {
    &:nth-child(1) {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    &:nth-child(2) {
      margin-left: 16px;
      padding: 4px 8px;
      font-size: 10px;
      border: 1px solid #3a509b;
      border-radius: 4px;
    }
  }
}

%listItemButton {
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: 1px solid rgba($color: #707070, $alpha: 0.5);
  cursor: pointer;
  opacity: 0.75;
  & > *:nth-child(2) {
    margin-left: 8px;
  }
  &:hover {
    opacity: 1;
  }
}

.listItemButtonData {
  @extend %listItemButton;
  grid-area: data;
  margin-left: 8px;
  padding: 0 8px;
}

.listItemButtonEdit {
  @extend %listItemButton;
  grid-area: edit;
  padding: 0 8px;
}

.listItemButtonDelete {
  @extend %listItemButton;
  grid-area: delete;
  padding-left: 8px;
}

.stage2 {
  & > *:not(:nth-child(1)):not(:nth-child(2)) {
    margin-top: 48px;
  }
}

.subtitle {
  font-size: 20px;
}

.section {
  display: flex;
  flex-direction: column;
}

@media (min-width: 1200px) {
  .section {
    flex-direction: row;
  }
}

@media (min-width: 1200px) {
  .aside {
    margin-right: 48px;
    width: 320px;
  }
}

.main {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
}

@media (min-width: 1200px) {
  .main {
    flex: 1;
    margin-top: 0;
  }
}

.subtitleWrapper {
  display: flex;
  align-items: center;
  & > *:not(:first-child) {
    margin-left: 8px;
  }
}

.warningText {
  color: #ef336a;
  font-weight: 700;
}

.warningIcon {
  width: 18px;
}

.toggle {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
  &Icon {
    position: absolute;
    left: -12px;
    width: 8px;
    transition: all 0.2s;
  }
}

.description {
  font-size: 13px;
  line-height: 1.4;
}

.brandSelector {
  margin: -4px -8px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

@media (min-width: 1200px) {
  .brandSelector {
    flex-direction: row;
  }
}

.brandButton {
  margin: 4px 8px;
  height: 28px;
  padding: 0 16px;
  border: 1px solid transparent;
  border-radius: 6px;
  transition: opacity 0.2s;
  opacity: 0.75;
  &:hover {
    opacity: 1;
  }
  &Active {
    border-color: #8e8e63;
    opacity: 1;
  }
}

.wordSelector,
.searchTable {
  margin-top: 16px;
}

%button {
  font-size: 16px;
  border-radius: 4px;
}

.backButton {
  @extend %button;
  height: 28px;
  padding: 0 16px;
  font-size: 14px;
  color: #242b43;
  background-color: #e0e0e0;
}

.nextButton {
  @extend %button;
  min-width: 112px;
  height: 40px;
  color: #f7f7f7;
  background-color: #41b167;
}

@media (min-width: 768px) {
  .stage1 {
    .nextButton {
      align-self: flex-end;
    }
  }

  .stage2 {
    .nextButton {
      align-self: center;
    }
  }
}
