.searchNameModal {
  width: calc(100vw - 96px);
  max-width: 624px;
  display: flex;
  flex-direction: column;

  & > * {
    margin: 8px;
  }

  .label {
    font-size: 14px;
  }

  .greenButton {
    align-self: flex-end;
  }
}

@media (min-width: 992px) {
  .searchNameModal {
    flex-direction: row;
    align-items: center;
  }
}

@media (min-width: 992px) {
  .inputWrapper {
    flex: 1;
  }
}

.input {
  background-color: transparent !important;
  border: none;
  border-bottom: 1px solid rgba($color: #707070, $alpha: 0.5) !important;
  border-radius: 0;
}

.greenButton {
  width: 112px;
  min-width: 112px;
  height: 40px;
  font-size: 16px;
  color: #f7f7f7;
  background-color: #41b167;
  border-radius: 4px;
}
