.filter {
  position: relative;
  margin-bottom: 16px;

  &Icon {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 20px;
  }
}

.input {
  input {
    padding-left: 40px;
  }
}

.main {
  height: 425px;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0px 3px 6px #00000029;
  overflow: hidden;
}

[dark-theme] {
  .main {
    background-color: #242b4384;
    box-shadow: none;
  }
}

.preloader {
  height: 100%;
}

.message {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  font-size: 11px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  opacity: 0.5;
}

.tableWrapper {
  flex: 1;
  font-size: 12px;
  text-align: left;
  overflow: auto;

  table {
    border-collapse: collapse;
    width: 100%;
  }

  tr {
    border-bottom: 1px solid rgba($color: #707070, $alpha: 0.25);
  }

  th {
    position: sticky;
    top: 0;
    height: 40px;
  }

  td {
    height: 32px;
    vertical-align: middle;
  }

  th,
  td {
    padding: 0 16px;
    white-space: nowrap;
    &.expandableCell {
      max-width: 1px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}

.sortingIcon {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  &Down {
    transform: translateY(-50%) rotate(180deg);
  }
}

.trashIcon {
  &:hover {
    color: #ef336a;
  }
}

.trashIcon,
.checkboxIcon {
  cursor: pointer;
}

.warning {
  line-height: 32px;
  padding: 0 16px;
  text-align: center;
  color: #ef336a;
}

.paginationWrapper {
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pagination {
  display: flex;
  padding: 0;
  li {
    display: inline-block;
    font-size: 0;

    &:not(:first-child) {
      margin-left: 8px;
    }

    a {
      width: 24px;
      height: 24px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 12px;
      cursor: pointer;
      border: 1px solid rgba($color: #707070, $alpha: 0.25);
      border-radius: 6px;
      transition: all 0.2s;

      &:hover {
        text-decoration: none;
        border: 1px solid rgba($color: #707070, $alpha: 0.5);
      }
    }
  }
}

.activeLink {
  border: 1px solid rgba($color: #707070, $alpha: 1) !important;
}
