.wordSelector {
  display: grid;
  grid-template:
    "a a" auto
    "b c" auto / minmax(0, 1fr) minmax(0, 1fr);
  grid-gap: 16px;
  & > div {
    &:nth-child(1) {
      grid-area: a;
    }
    &:nth-child(2) {
      grid-area: b;
    }
    &:nth-child(3) {
      grid-area: c;
    }
  }
}

.wordsArea {
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0px 3px 6px #00000029;
  overflow: hidden;
  position: relative;
  &Big {
    height: 256px;
  }
  &Small {
    height: 160px;
  }
}

[dark-theme] {
  .wordsArea {
    background-color: #242b4384;
    box-shadow: none;
  }
}

.preloader {
  height: 100%;
}

.message {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  font-size: 11px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  opacity: 0.5;
}

.wordsWrapper {
  height: 100%;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0.025);
  }
  &::-webkit-scrollbar-thumb {
    border: none;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 0;
  }
}

.words {
  display: flex;
  flex-wrap: wrap;
}

.wordsArea {
  &Big {
    .words {
      padding: 8px 24px;
    }
  }
  &Small {
    .words {
      padding: 8px;
    }
  }
}

.word {
  margin: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 32px;
  padding: 0 8px;
  font-size: 12px;
  border: 1px solid #707070;
  border-radius: 6px;
  user-select: none;

  & > div {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  & > svg {
    margin-left: 4px;
    flex-shrink: 0;
    opacity: 0.75;
    cursor: pointer;
    &:hover {
      opacity: 1;
    }
  }
}

.wordsArea {
  &Big {
    .word {
      width: calc((100% - 32px) / 2);
    }
  }
  &Small {
    .word {
      width: calc(100% - 16px);
    }
  }
}

@media (min-width: 768px) {
  .wordsArea {
    &Big {
      .word {
        width: calc((100% - 64px) / 4);
      }
    }
    &Small {
      .word {
        width: calc((100% - 32px) / 2);
      }
    }
  }
}

@media (min-width: 992px) {
  .wordsArea {
    &Big {
      .word {
        width: calc((100% - 96px) / 6);
      }
    }
    &Small {
      .word {
        width: calc((100% - 48px) / 3);
      }
    }
  }
}

@media (min-width: 1200px) {
  .wordsArea {
    &Big {
      .word {
        width: calc((100% - 64px) / 4);
      }
    }
    &Small {
      .word {
        width: calc((100% - 32px) / 2);
      }
    }
  }
}

@media (min-width: 1366px) {
  .wordsArea {
    &Big {
      .word {
        width: calc((100% - 96px) / 6);
      }
    }
    &Small {
      .word {
        width: calc((100% - 48px) / 3);
      }
    }
  }
}

.row {
  position: relative;
  margin-top: 16px;
}

.input {
  input {
    padding-right: 40px;
  }
}

.plusButton {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 40px;
  font-size: 0;
}
